/* Default Whatsapp Form CSS by www.idblanter.com */
form.whatsapp-form {
    -webkit-box-shadow: 0px 20px 60px 20px #961a1d, 20px 16px 60px #ca2327;
    box-shadow: 0px 20px 60px 20px #961a1d, 20px 16px 60px #ca2327;
    border-radius: .5rem;
    padding: 20px;
    box-sizing: border-box;
    color: #444;
    font-size: 14px;
    line-height: 1.5;
}
 .whatsapp-form a.send_form {
    color: #fff;
    background: #21a51f;
    text-decoration: none;
    display: inline-block;
    padding: 10px 25px;
    border-radius: .3rem;
    font-weight: 700;
    letter-spacing: .5px;
    font-size: 15px;
}
#text-info span {
    display: block;
    padding: 10px 15px;
    text-align: center;
    font-weight: 700;
    margin: 15px 0;
    border-radius: .5rem;
}
#text-info span.yes {
    background: #c6ffc5;
    color: #0ea904;
}
#text-info span.no {
    background: #ffc5c5;
    color: #ce0404;
}
.whatsapp-form{
  width:100%;
  max-width:700px;
  margin:0 auto;
  box-sizing:border-box;
}
.divContact{

        content: 'Hi';
        position: relative;
        bottom: -5px;
        left: 70%;
        background: black;
        color: #fff;
        width: 320px;
        padding: 16px 4px 16px 0;
        border-radius: 6px;
        font-size: 13px;

}
.divContact span{
    font: normal normal normal 14px/1 FontAwesome;
        margin: 0 5px 0 15px;

}
@media (max-width: 991px){
    .divContact{
        position: unset;
    }
}