.Mui-selected {
  background-color: #c42427 !important;
  color: #e8e8e8;
}
.TabButton {
  margin-left: 10px;
}
/* From cssbuttons.io by @alexroumi */
.TabButton {
  padding: 15px 25px;
  border: unset;
  border-radius: 15px;
  color: #212121;
  z-index: 1;
  background: #e8e8e8;
  position: relative;
  font-weight: 1000;
  font-size: 17px;
  -webkit-box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, 0.27);
  box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, 0.27);
  transition: all 250ms;
  overflow: hidden;
}

.TabButton:hover::before {
  width: 100%;
}
@media (max-width: 991px) {
  .TabButton {
    padding: 8px 15px;
    font-size: 10px;
  }
}
